<template>
  <div>
    <base-header
      title="유니트"
      :mainImage="require('../assets/images/bg.jpg')"
      :tabs="tabs"
      @changeTab="changeType"
    />
    <v-container class="contents">
      <div v-if="currentType" class="col-md-12 col-sm-12 col-xs-12">
        <p class="display-1 mb-0 pt-5">{{ currentType.title }} 타입 평면도</p>
        <v-img class="align-end" :src="currentType.image"></v-img>
      </div>
    </v-container>
  </div>
</template>

<script>
import BaseHeader from "@/components/layout/BaseHeader";

export default {
  components: {
    BaseHeader
  },
  data() {
    return {
      tabs: [
        {
          id: "59a",
          title: "59a",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_59a.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: true
        },
        {
          id: "59b",
          title: "59b",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_59b.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        },
        {
          id: "59c",
          title: "59c",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_59c.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        },
        {
          id: "75a",
          title: "75a",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_75a.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        },
        {
          id: "75b",
          title: "75b",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_75b.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        },
        {
          id: "84a",
          title: "84a",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_84a.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        },
        {
          id: "84b",
          title: "84b",
          image:
            "http://yaglobal.cafe24.com/boramea/images/contents/unit_84b.jpg",
          tabIcon: "mdi-format-list-bulleted-type",
          active: false
        }
      ],
      currentType: {}
    };
  },
  methods: {
    changeType(typeId) {
      this.tabs.forEach(t => {
        t.active = typeId == t.id;
      });
      this.currentType = this.tabs.find(t => t.active);
    }
  },
  mounted() {
    if (this.tabs) {
      let houseType = this.tabs[0];
      if (this.$route.params && this.$route.params.type) {
        houseType = this.tabs.find(t => t.id === this.$route.params.type);
      }
      this.changeType(houseType.id);
    }
  }
};
</script>



